import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import horologium from "./horologium.png";
import leviathans_maw from "./leviathans_maw.png";
import niroki_vela from "./niroki_vela.png";
import sohly_tair from "./sohly_tair.png";
import the_north_star from "./the_north_star.png";
import troubleshooters_den from "./troubleshooters_den.png";

class Castorix_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };

    const slideImages = [
      horologium,
      leviathans_maw,
      niroki_vela,
      sohly_tair,
      the_north_star,
      troubleshooters_den,
    ];

    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == horologium) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Horologium</b></p>
                            <p className="caption_description">
                              Named after the Castorixi constellation, Horologium is arguably the sector’s most beautiful city: located under Mount Muun, the tallest mountain within the territory, the metropolis is a cavernous arctic utopia, acclaimed as a city of starlight once the sun departs the horizon.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == leviathans_maw) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Leviathan's Maw</b></p>
                            <p className="caption_description">
                              Built around the bones of an eons-old leviathan, the Maw is a trading harbor city that lines the frigid waters of Castorix’s coast. Those who wish to get a taste of Castorix’s most unique sea cuisine must remember to bundle up, as the journey is just as wicked as one might expect from the coldest territory of Koranin.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == niroki_vela) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Niroki Vela</b></p>
                            <p className="caption_description">
                              “Freezing Point” in the Ancient Tongue, Niroki Vela is the largest of Castorix’s many mining areas. It digs deep under the Sorcerai Mountain Range, a quarry for precious minerals of upmost importance to Koranin’s livelihood.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == sohly_tair) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Sohly Tair</b></p>
                            <p className="caption_description">
                              A popular casino in Castorix’s entertainment court, where the rich and powerful like to frequent. Its many rooms offer more than just a night of fun from money-grabbing machines, but beware—a step into a room with the wrong people may just end with a knife in one’s back.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == the_north_star) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The North Star</b></p>
                            <p className="caption_description">
                              The capital of Castorix, a lively city that stands at the entrance to the Underground. Leading into the cities within the Winter Sector’s infamous onyx mountains, one must remember to always keep their NightSight Elixir close…
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == troubleshooters_den) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Troubleshooter's Den</b></p>
                            <p className="caption_description">
                              An arena befit for all inventors of mechanized robots, where mechanics gather together to show off their latest creations in one-versus-one battles of human-controlled fighting machines, from mecha robots to bipedal walkers, in hopes of gaining entry into Castorix’s top technology businesses.
                            </p>
                          </div>
                        </div>
                      )
                    }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Castorix_Gallery;