import React, { useState, useEffect, useRef, useLayoutEffect } from 'react';
import { NavLink, Link, useLocation } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';
import '../../css/Sectors.scss';
import splash_alcerium from "../../img/splash_map.png";
import arrow from '../../img/down_arrow.png';
import jokerIcon from '../../img/joker_icon.png';
import { sectors } from './Sectors_Config';

const Alcerium = () => {
    const [currSlide, setCurrSlide] = useState(0);
    const ref = useRef(null);
    const location = useLocation();

    const next = () => {
        if (currSlide >= 6) {
            setCurrSlide(0);
        }
        else {
            setCurrSlide(currSlide + 1);
        }
    }

    const desired = (e) => {
        setCurrSlide(Number(e.target.id))
    }

    useEffect(() => {
        ref.current.style.transition = 'all 0.2s ease-in-out';
        ref.current.style.transform = `translateX(-${currSlide}00%)`;
    }, [currSlide])

    useLayoutEffect(() => {
        setCurrSlide(0);
        window.scrollTo(0, 0);
    }, [location.pathname])

    AOS.init({
        duration: 1200,
    })

    return (
        <div className="sector_body">
            <NavLink to="/joker"><img src={jokerIcon} className="jokerIcon" alt="joker icon" /></NavLink>
            {sectors.map((sector, index) =>
                sector.name.toLowerCase() === window.location.href.split("/")[window.location.href.split("/").length - 1]
                &&
                <div className="sector_details">
                    <div className="sector_section" key={index}>
                        <div className="sector_title">{sector.name.toUpperCase()}</div>
                        <img src={sector.splash} className="sector_splash" alt="character splash art" />
                        <div className="sector_excerpt_container">
                            <img
                                src={sector.icon}
                                className="sector_excerpt_icon"
                                alt="sector icon"
                            />
                            <p className="sector_excerpt">{sector.main_description}</p>
                        </div>
                    </div>
                    <div data-aos="fade-up" className="places_section">
                        <div className='places_section_wrapper'>
                            <div className='places_section_mask'>
                                <div class="places_section_mask_circle_outer"></div>
                                <div class="places_section_mask_circle_inner"></div>
                                <div className='places_section_container' ref={ref}>
                                    {sector.places.map((p, i) =>
                                        <div key={i}>
                                            <img src={p.img} className='places_section_container_img' />
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className='places_section_button_container'>
                                <img src={arrow} onClick={next} className='places_section_button' />
                            </div>
                            {sector.places.map((p, i) =>
                                p.id === currSlide
                                &&
                                <div>
                                    <div key={i} className='places_section_info'>
                                        <div className='places_section_info_name'>{p.name}</div>
                                        <div className='places_section_info_description'>{p.description}</div>
                                    </div>
                                    <div className='places_section_info_nav'>
                                        {sector.places.map((p, i) =>
                                            <div key={i}>
                                                <img
                                                    src={p.img}
                                                    className={`places_section_info_nav_items ${p.id === currSlide && 'places_section_info_nav_items_active'}`}
                                                    onClick={desired}
                                                    id={p.id}
                                                    key={p.id}
                                                />
                                            </div>
                                        )}
                                    </div>
                                </div>
                            )}

                        </div>
                    </div>
                    <div data-aos="fade-up" className="affiliations_section">
                        <div className=''>
                            {/*sector.affiliations.map((a, i) =>
                                <div key={i}>
                                    {a.name}
                                </div>
                                        )*/}
                        </div>
                    </div>
                    <div data-aos="fade-up" className="sector_selection">
                        {
                            sectors.map((sector) =>
                                <Link to={"/world" + sector.url} style={{ textDecoration: 'none' }}>
                                    <img src={sector.icon} className="sector_selection_icons" />
                                </Link>
                            )
                        }
                    </div>
                </div>
            )}
            <div className="">
                <p className="selectInstruct"></p>
            </div>
            <br></br>
        </div>
    )
}

export default Alcerium;