import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import angelfall from "./angelfall.png";
import arkenal from "./arkenal.png";
import excelia from "./excelia.png";
import harlequins_crown from "./harlequins_crown.png";
import palettius from "./palettius.png";
import the_library_of_mokka from "./the_library_of_mokka.png";

class Valterre_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };
    const slideImages = [
      angelfall,
      arkenal,
      excelia,
      harlequins_crown,
      palettius,
      the_library_of_mokka,
    ];
    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == angelfall) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Angelfall</b></p>
                            <p className="caption_description">
                              A symbol of peace within the continent, Angelfall is built around the ruins of the ancient war, a renovated cathedral that also hosts the meetings of the Council of High Mages.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == arkenal) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Arkenal Prison</b></p>
                            <p className="caption_description">
                              A menacing spiral tower that serves as the most heavily guarded prison in Valterre and is graced with the presence of Koranin’s most villainous minds. The structure has never seen the light of day, perhaps further corrupting this section of Valterre…
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == excelia) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Excelia</b></p>
                            <p className="caption_description">
                              The heart of Koranin and the pride of Valterre. The city is run by high-ranking officials and serves as the capital of all sectors. But to the common folk, it is the number one region for tourism and education, as it is home to the esteemed Excelia Academy.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == harlequins_crown) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Harlequin’s Crown</b></p>
                            <p className="caption_description">
                              The circular mountain range that surrounds Valterre and is home to the exquisite rock formations of the Rouche Caverns.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == palettius) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Palettius</b></p>
                            <p className="caption_description">
                              Valterre’s artisan quarter, a place of dreams for appreciators of the fine arts. Each street is named after a different art medium, and envelopes countless galleries, museums, and ateliers for renowned creators and apprentices.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == the_library_of_mokka) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The Library of Mokka</b></p>
                            <p className="caption_description">
                              One of the largest and most significant libraries of Koranin, boasting a collection as old as the land itself and guarded by a creature even older. One must always remember to keep quiet, or the Bookworm may just swallow them whole—literally.
                            </p>
                          </div>
                        </div>
                      )
                    }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Valterre_Gallery;