import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './css/Character.scss';
import KingpinChara from "./components/kingpin/Kingpin_Chara";
import IncarnateChara from "./components/incarnate/Incarnate_Chara";
import jokerIcon from './img/joker_icon.png';
import kingpin_splash from './img/splash_kingpin.png';
import border from './img/border_bottom.svg';

const Character = () => {
    const [active, setActive] = useState("kingpin");

    const _onClick = (story) => {
        setActive(story);
        console.log("e", active)
    }

    AOS.init({
        duration: 1200,
    })

    return (
        <div className="character_body">
            <NavLink to="/joker"><img src={jokerIcon} className="jokerIcon" alt="joker icon" /></NavLink>
            <div className="character_details">
                <div className="character_section">
                    <div className="test">
                        <img src={kingpin_splash} className="character_splash" alt="character splash art" />
                        <div className="character_excerpt_container">
                            {active === "kingpin"
                                &&
                                <p className="character_excerpt">
                                    Six students, five sectors, four pieces of a card spell powerful enough to save Koranin from the clutches of evil, or bring it closer and closer to the brink of humanity's destruction. They say not to bite the hand that feeds you, but what happens when you discover the hand has been poisoning you all along?
                                </p>
                            }
                            {active === "incarnate"
                                &&
                                <p className="character_excerpt">
                                    Coming soon...
                                </p>
                            }
                        </div>
                        <img src={border} className="character_border" alt="character splash art" />
                    </div>
                    <div className="character_story" data-aos="fade-up">
                        <div className={active === "kingpin" ? "character_story_selection" : "character_story_selection character_story_selection_inactive"}>
                            <img
                                src={jokerIcon}
                                className="character_story_selection_icon"
                                onClick={() => _onClick("kingpin")}
                                alt="kingpin icon"
                            />
                            <h2 className="character_story_selection_title">Kingpin</h2>
                        </div>
                        <div className={active === "incarnate" ? "character_story_selection" : "character_story_selection character_story_selection_inactive"}>
                            <img
                                src={jokerIcon}
                                className="character_story_selection_icon"
                                onClick={() => _onClick("incarnate")}
                                alt="incarnate icon"
                            />
                            <h2 className="character_story_selection_title">Incarnate</h2>
                        </div>
                    </div>
                </div>
                {active === "kingpin" && <div data-aos="fade-up"><KingpinChara /></div>}
                {active === "incarnate" && <div data-aos="fade-up"><IncarnateChara /></div>}
            </div>
            <br></br>
        </div>
    );
}

export default Character;