import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import bazaar_of_kirci from "./bazaar_of_kirci.png";
import kozen_shrine from "./kozen_shrine.png";
import port_of_royals from "./port_of_royals.png";
import purblinde from "./purblinde.png";
import regavia from "./regavia.png";
import the_southern_sun from "./the_southern_sun.png";

class Lysande_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };

    const slideImages = [
      bazaar_of_kirci,
      kozen_shrine,
      port_of_royals,
      purblinde,
      regavia,
      the_southern_sun,
    ];

    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} figcaption="sample" />
                <div className="caption">
                  {(() => {
                    if (each == bazaar_of_kirci) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Bazaar of Kirci</b></p>
                            <p className="caption_description">
                              A sweeping street market dedicated to the ancient goddess of jinnir, consisting of four intertwining circular roads each providing specialized goods: the Alley of Spices, for food-related wares; the Alley of Fabrics, for textile-related wares, the Alley of Metals, for weapon-related wares; and the Alley of Spells, for magic-related wares.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == kozen_shrine) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Kozen Shrine</b></p>
                            <p className="caption_description">
                              A floating abode where the purest of hearts maintained the flow of moral enchantments in Lysande and located between the border of Lysande and Lyrisius, though good luck finding its location with its spirit-enchanted barrier in place.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == port_of_royals) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Port of Royals</b></p>
                            <p className="caption_description">
                              Lysande’s capital port city is the world’s most prominent transportation intersection and is never seen without boisterous activity from day to night. The city is one of the most carefree and diverse, welcoming many to the eternal land of summer.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == purblinde) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Purblinde District</b></p>
                            <p className="caption_description">
                              An illegal red-light district, where black market sales and brothels are around every corner and often frequented by spirits. How the district continues to flourish without government interference is a mystery…
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == regavia) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Regavia</b></p>
                            <p className="caption_description">
                              An esteemed weapons forge recognized for its mass production of weaponry to all of Koranin, but also for its contribution of multiple innovative weapon types over the centuries.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == the_southern_sun) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The Southern Sun</b></p>
                            <p className="caption_description">
                              A kingdom of sand that lies in the heart of the Doreau Desert, protected by a colossal magic-induced barrier to stave off relentless sandstorms and hot winds. A magical city frequented by tourists for its exceptional architecture and sensational training arenas.
                            </p>
                          </div>
                        </div>
                      )
                    }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Lysande_Gallery;