import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import depths_of_valerim from "./depths_of_valerim.png";
import karable from "./karable.png";
import leikhazi_gardens from "./leikhazi_gardens.png";
import the_anglerfish from "./the_anglerfish.png";
import tohzas_end from "./tohzas_end.png";
import tora_vigilinthe from "./tora_vigilinthe.png";
import veruphia from "./veruphia.png";

const places = [
  {
    name: "Depths of Valerim",
    img: "depths_of_valerim",
    description: "What was once a beautiful, bustling underwater castle village ruled by royal merfolk is now wasted wreckage, mostly forgotten at the bottom of the sea. The kingdom used to be an iconic symbol of peace and hope for underwater creatures…"
  }
]

class Alcerium_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      easing: "ease",
      pauseOnHover: true,
      indicators: (i) => <div className="indicator"></div>
    };

    const slideImages = [
      depths_of_valerim,
      karable,
      leikhazi_gardens,
      the_anglerfish,
      tohzas_end,
      tora_vigilinthe,
      veruphia,
    ];

    return (
      <div className="App" >
        <div className="slide-container" >
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide" >
                <img className="lazy" src={each} alt="" />
                <div className="caption">
                  {(() => {
                    if (each == depths_of_valerim) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Depths of Valerim</b></p>
                            <p className="caption_description">
                              What was once a beautiful, bustling underwater castle village ruled by royal merfolk is now wasted wreckage, mostly forgotten at the bottom of the sea. The kingdom used to be an iconic symbol of peace and hope for underwater creatures…
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == karable) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Karable</b></p>
                            <p className="caption_description">
                              The largest port linking Alcerium’s rainforest and ocean territory is a resource center for those wishing to cross to land or take the Tubes into the Undersea: one of the most fantastic tourist attractions for both sea and land beings.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == leikhazi_gardens) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Leikhazi Gardens</b></p>
                            <p className="caption_description">
                              An enchanting city of flora, filled with colorful blossoms and fruits as far as the eye can see. Don’t be deceived by its beauty, for something poisonous may lay within...
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == the_anglerfish) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The Anglerfish</b></p>
                            <p className="caption_description">
                              A buoyant, serpent-shaped fishing city built on flotsam and magic that resides just off the coast of Zenith‘s Bay. Frequented by pirates and tradesmen for the delectable sea catches that seemingly only appear around the floating city at certain hours of the day.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == tohzas_end) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Tohza’s End</b></p>
                            <p className="caption_description">
                              A treehouse paradise deep within Alcerium’s legendary rainforests. The community is built around an olden grandfather tree, cared for by some of the most ancient species of Koranin.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == tora_vigilinthe) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Tora Vigilinthe</b></p>
                            <p className="caption_description">
                              “Tower of Vigilance” in the Ancient Tongue, the Tora Vigilinthe is Koranin’s most famous institute of healing. A towering obelisk, it is separated into three main bodies for non-magic-using students, magic-using students, and researchers of the healing arts.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == veruphia) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Veruphia</b></p>
                            <p className="caption_description">
                              Nicknamed the Jewel Box of Alcerium, Veruphia is a rectangular, box-shaped city situated underwater that floats just above the Hypis Coral Reef. Made of a thick, crystal-like alloy to entrap air, the metropolis is one of the most beautiful locations within the sector, and is a prime location for tourists due to its accessibility for those who lack aquatic respiration.
                            </p>
                          </div>
                        </div>
                      )
                    }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Alcerium_Gallery;