import React, { useEffect } from 'react';
import { HashRouter, Routes, Route } from "react-router-dom";
import './index.css';
import './App.css';
import NavBar from "./NavBar";
import Home from './pages/Home';
import World from './pages/World';
import Alcerium from './pages/components/sectors/Sectors';
import Castorix from './pages/components/sectors/Sectors';
import Valterre from './pages/components/sectors/Sectors';
import Lyrisius from './pages/components/sectors/Sectors';
import Lysande from './pages/components/sectors/Sectors';
import Character from './pages/Character';
import Joker from './pages/Joker';
import About from './pages/About';

const App = () => {
  useEffect(() => {
    document.title = "kingpin"
  }, []);

  return (
    <div className="App">
      <HashRouter basename="/">
        <NavBar />
        <Routes>
            <Route exact="true" path='/' element={<Home/>} />
            <Route exact="true" path="/world" element={<World/>} />
            <Route exact="true" path='/world/alcerium' element={<Alcerium/>} />
            <Route exact="true" path='/world/castorix' element={<Castorix/>} />
            <Route exact="true" path='/world/valterre' element={<Valterre/>} />
            <Route exact="true" path='/world/lyrisius' element={<Lyrisius/>} />
            <Route exact="true" path='/world/lysande' element={<Lysande/>} />
            <Route exact="true" path="/character" element={<Character/>} />
            <Route exact="true" path="/joker" element={<Joker/>} />
            <Route exact="true" path="/about" element={<About/>} />
        </Routes>
      </HashRouter>
      <link rel="icon" href="./pages/img/joker_icon.png"></link>
      <br></br>
    </div>
  );
}

export default App;