import React, { Component } from "react";
import { Slide } from "react-slideshow-image";
import "../Slideshow.css"
import "react-slideshow-image/dist/styles.css";
import champions_circle from "./champions_circle.png";
import cometere from "./cometere.png";
import havensworth from "./havensworth.png";
import leaves_of_dreamers_songs from "./leaves_of_dreamers_songs.png";
import the_elite_court from "./the_elite_court.png";
import zeronyn from "./zeronyn.png";

class Lyrisius_Gallery extends Component {
  constructor() {
    super();
    this.slideRef = React.createRef();
    this.back = this.back.bind(this);
    this.next = this.next.bind(this);
    this.state = {
      current: 0
    };
  }

  back() {
    this.slideRef.current.goBack();
  }

  next() {
    this.slideRef.current.goNext();
  }

  render() {
    const properties = {
      duration: 5000,
      autoplay: true,
      transitionDuration: 500,
      arrows: false,
      infinite: true,
      pauseOnHover: true,
      easing: "ease",
      indicators: (i) => <div className="indicator">{i + 1}</div>
    };

    const slideImages = [
      champions_circle,
      cometere,
      havensworth,
      leaves_of_dreamers_songs,
      the_elite_court,
      zeronyn
    ];

    return (
      <div className="App">
        <div className="slide-container">
          <Slide ref={this.slideRef} {...properties}>
            {slideImages.map((each, index) => (
              <div key={index} className="each-slide">
                <img className="lazy" src={each} alt="sample" />
                <div className="caption">
                  {(() => {
                    if (each == champions_circle) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Champion’s Circle</b></p>
                            <p className="caption_description">
                              Lyrisius’ largest underground Gladiator ring, where many of the sector’s stealthiest fighters and criminals are indentured to fight to the death for the entertainment of spectating elites. Though many gain glory, countless more earn death—and only a select few ever manage to purchase their freedom.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == cometere) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Cometére</b></p>
                            <p className="caption_description">
                              A magical traveling market that only appears in Havensworth for a week every five years, selling the best enchanted items from food to elixirs to weaponry to ancient artifacts. One of the most anticipated events of Koranin, where citizens across all sectors flock to delight in what the marketplace has to offer.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == havensworth) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Havensworth</b></p>
                            <p className="caption_description">
                              Lyrisius’ most prominent feature: countless floating islands that make up the middle and upper-class housing and entertainment. Havensworth is also the host of Cometére: a magical traveling market that only appears for a week every five years.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == leaves_of_dreamers_songs) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Leaves of Dreamers' Songs</b></p>
                            <p className="caption_description">
                              A well-known forest among Zeronyists living in the Streets of Regul but a place left untouched out of fear that those who do not have wealth nor status will ‘taint’ the sacred grounds of locked doors and dreams. The lowest-class citizens often pray along the circular border of the trees leading into the forest, which guards a legacy buried within the sector.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == the_elite_court) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>The Elite Court</b></p>
                            <p className="caption_description">
                              A series of isolated floating palaces, one of the wealthiest and most secure places in all of Lyrisius, in Havensworth above the capital city, Autu, and home to Lyrisius’ elite houses: Aver, Chevalier, Fyvira, Paldeu and Wysterié.
                            </p>
                          </div>
                        </div>
                      )
                    } else if (each == zeronyn) {
                      return (
                        <div className="sector_img_caption">
                          <div className="caption_padding">
                            <p className="caption_title"><b>Zeronyn</b></p>
                            <p className="caption_description">
                              The ground terrain of Lyrisius, occupied by Lyrisius’ lower and working class, though frequented by all for its autumnal scenery and access to transportation, though unacknowledged by most of the elites that live in the skies.
                            </p>
                          </div>
                        </div>
                      )
                    }
                  })()}
                </div>
              </div>
            ))}
          </Slide>
        </div>
      </div>
    );
  }
}

export default Lyrisius_Gallery;