import React, { useState, useRef } from 'react';
import '../css/Joker.css';
import Joker_Alcerium from './joker/Joker_Alcerium.js';
import Joker_Castorix from './joker/Joker_Castorix.js';
import Joker_Lyrisius from './joker/Joker_Lyrisius.js';
import Joker_Lysande from './joker/Joker_Lysande.js';
import Joker_Valterre from './joker/Joker_Valterre.js';
import arrow from '../img/arrow_white.png';
import alceriumIcon from '../img/alcerium_iconWhite.png';
import alceriumIconRed from '../img/alcerium_iconRed.png';
import castorixIcon from '../img/castorix_iconWhite.png';
import castorixIconRed from '../img/castorix_iconRed.png';
import lyrisiusIcon from '../img/lyrisius_iconWhite.png';
import lyrisiusIconRed from '../img/lyrisius_iconRed.png';
import lysandeIcon from '../img/lysande_iconWhite.png';
import lysandeIconRed from '../img/lysande_iconRed.png';
import valterreIcon from '../img/valterre_iconWhite.png';
import valterreIconRed from '../img/valterre_iconRed.png';

class Joker_Selection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showJokerAlcerium: false, 
            showJokerCastorix: false,
            showJokerValterre: false,
            showJokerLyrisius: false,
            showJokerLysande: false
        };
    }


    toggleJokerAlcerium = () => {
        this.setState({ 
            showJokerAlcerium: true, 
            showJokerCastorix: false, 
            showJokerValterre: false,
            showJokerLyrisius: false,
            showJokerLysande: false,
        });
        window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    toggleJokerCastorix = () => {
        this.setState({ 
            showJokerAlcerium: false, 
            showJokerCastorix: true, 
            showJokerValterre: false,
            showJokerLyrisius: false,
            showJokerLysande: false,
        });
        window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    toggleJokerValterre = () => {
        this.setState({ 
            showJokerAlcerium: false, 
            showJokerCastorix: false, 
            showJokerValterre: true, 
            showJokerLyrisius: false,
            showJokerLysande: false,
        });
        window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    toggleJokerLyrisius = () => {
        this.setState({ 
            showJokerAlcerium: false, 
            showJokerCastorix: false, 
            showJokerValterre: false,
            showJokerLyrisius: true, 
            showJokerLysande: false,
        });
        window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    toggleJokerLysande = () => {
        this.setState({ 
            showJokerAlcerium: false, 
            showJokerCastorix: false, 
            showJokerValterre: false,
            showJokerLyrisius: false,
            showJokerLysande: true, 
        });
        window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    render() {
        return (
            <div className="joker_selection">
                <div className="X">
                    <div className="joker_selectionR1">
                        <img src={this.state.showJokerLyrisius ? lyrisiusIconRed : lyrisiusIcon}  className="jokerSectorIcons" onClick={this.toggleJokerLyrisius}/>
                    </div>
                    <br></br><br></br><br></br>
                    <div className="joker_selectionR2">
                        <img src={this.state.showJokerAlcerium ? alceriumIconRed : alceriumIcon} className="jokerSectorIcons" onClick={this.toggleJokerAlcerium}/>
                        <img src={this.state.showJokerValterre ? valterreIconRed : valterreIcon} className="jokerSectorIcons" onClick={this.toggleJokerValterre}/>
                        <img src={this.state.showJokerCastorix ? castorixIconRed : castorixIcon} className="jokerSectorIcons" onClick={this.toggleJokerCastorix}/>
                    </div>
                    <br></br><br></br><br></br>
                    <div className="joker_selectionR3">
                        <img src={this.state.showJokerLysande ? lysandeIconRed : lysandeIcon} className="jokerSectorIcons" onClick={this.toggleJokerLysande}/>
                    </div>
                </div>
                {this.state.showJokerAlcerium ? <div><br></br><br></br><br></br><div className='divider'></div>
                    <div className="topInstruct"><img src={arrow} className="arrow" /><p className="jarrow_instruct">scroll to view affiliations</p></div><br></br><div className='divider'></div>
                    <Joker_Alcerium/><div className='divider2'></div><br></br><br></br>
                    <p className="arrow_instruct">click to return to joker sector selection</p><img src={arrow} className="upArrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    </div> : null}
                {this.state.showJokerCastorix ? <div><br></br><br></br><br></br><div className='divider'></div>
                    <div className="topInstruct"><img src={arrow} className="arrow" /><p className="jarrow_instruct">scroll to view affiliations</p></div><br></br><div className='divider'></div>
                    <Joker_Castorix/><div className='divider2'></div><br></br><br></br>
                    <p className="arrow_instruct">click to return to joker sector selection</p><img src={arrow} className="upArrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    </div> : null}
                {this.state.showJokerValterre ? <div><br></br><br></br><br></br><div className='divider'></div>
                    <div className="topInstruct"><img src={arrow} className="arrow" /><p className="jarrow_instruct">scroll to view affiliations</p></div><br></br><div className='divider'></div>
                    <Joker_Valterre/><div className='divider2'></div><br></br><br></br>
                    <p className="arrow_instruct">click to return to joker sector selection</p><img src={arrow} className="upArrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    </div> : null}
                {this.state.showJokerLyrisius ? <div><br></br><br></br><br></br><div className='divider'></div>
                    <div className="topInstruct"><img src={arrow} className="arrow" /><p className="jarrow_instruct">scroll to view affiliations</p></div><br></br><div className='divider'></div>
                    <Joker_Lyrisius/><div className='divider2'></div><br></br><br></br>
                    <p className="arrow_instruct">click to return to joker sector selection</p><img src={arrow} className="upArrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    </div> : null}
                {this.state.showJokerLysande ? <div><br></br><br></br><br></br><div className='divider'></div>
                    <div className="topInstruct"><img src={arrow} className="arrow" /><p className="jarrow_instruct">scroll to view affiliations</p></div><br></br><div className='divider'></div>
                    <Joker_Lysande/><div className='divider2'></div><br></br><br></br>
                    <p className="arrow_instruct">click to return to joker sector selection</p><img src={arrow} className="upArrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    </div> : null}
            </div>
        )
    }
}

export default Joker_Selection;