import React, { useRef } from 'react'
import './css/Home.css';
import koranin_temp from './img/koranin_temp.jpg';
import alceriumIcon from './img/alcerium_icon.png';
import castorixIcon from './img/castorix_icon.png';
import systems_temp from './img/systems_temp.jpg';
import valterreIcon from './img/valterre_icon.png';
import lyrisiusIcon from './img/lyrisius_icon.png';
import lysandeIcon from './img/lysande_icon.png';
import down_arrow from './img/down_arrow.png';
import up_arrow from './img/up_arrow.png';
import Sectors from './components/Sectors';


function Home() {
  const sectorSection = useRef(null);
  const toSectorSection = () =>
    window.scrollTo({
      top: sectorSection.current.offsetTop,
      behavior: "smooth"
    });
  const rankSection = useRef(null);
  const toRankSection = () =>
    window.scrollTo({
      top: rankSection.current.offsetTop,
      behavior: "smooth"
    });
  const homeSection = useRef(null);
  const toHomeSection = () =>
    window.scrollTo({
      top: homeSection.current.offsetTop,
      behavior: "smooth"
    });

  return (
    <div className="body">
      {/* SECTION 1: KORANIN ----------------------------------------------------------------------------------------- */}
      <section>
        <div className="koranin">
          <img src={koranin_temp} ref={homeSection} className="main_image" />
          <div className="details">
            <div className="section">
              <h2 className="title_intro">THE WORLD OF</h2>
              <h2 className="title">Koranin</h2>
            </div>
            <div className="icon_section">
              <img src={alceriumIcon} className="icon" />
              <img src={castorixIcon} className="icon" />
              <img src={valterreIcon} className="icon" />
              <img src={lyrisiusIcon} className="icon" />
              <img src={lysandeIcon} className="icon" />
            </div>
            <p className="excerpt">
              <b>S</b>plit into five respective sectors, Koranin is a diverse world populated by humans and mythical beings alike—some of which who are gifted with a constellation-blessed affinity for magic. Governed by a council of High Mages, Koranin continues through its age of peace and prosperity. But when evil lurks within its roots, perhaps it is time for others to step forward and take matters into their own hands...
            </p>
            <img src={down_arrow} className="down_arrow" onClick={toSectorSection} />
            <p className="arrow_instruct">continue to system</p>
          </div>
        </div>
      </section>
      {/* SECTION 2: SECTORS ----------------------------------------------------------------------------------------- */}
      {/* CASTORIX 
      <section>
        <div className="sector" ref={sectorSection}>
          <div className="details">
            <Sectors />
          </div>
        </div>
      </section>
      */}
      <br></br><br></br><br></br><br></br>
      {/* SECTION 3: MAGE RANKS ----------------------------------------------------------------------------------------- */}
      <section>
        <div className="koranin">
          <img src={systems_temp} ref={rankSection} className="image" />
          <div className="details">
            <br></br><br></br><br></br><br></br><br></br><br></br><br></br><br></br>
            <p className="arrow_instruct">return to home</p>
            <img src={up_arrow} className="up_arrow" onClick={toSectorSection} />
            <div className="section">
              <br></br>
              <h2 className="title">System of Koranin</h2>
            </div>
            <br></br>
            <p className="excerpt">
              <h2 className="systemTitle">CARD DECK</h2>
              <p className="systemDetail">
                A spell deck granted to licensed magic users through the Resonyte process, in which High Mage Extractors transfer the mage's source of magic energy to the card deck. Doing so allows the mage to utilize their magic through spell cards that pose no harm to the user and permit more constant use of magic without dangerously draining their energy source. The cards' design personalizes accordingly upon forming a connection.
              </p>
              <h2 className="systemTitle">CONSTELLATIONS</h2>
              <p className="systemDetail">
                Minor deities that bless all mages that come to power. Upon blessing a mage born under the same star sign, the Blessed Mage's magic strength is considerably increased, escalating their potential to unlock their upper-level cards and greatly diminishing the repercussions of casting higher-level magic. Constellations may only bless mages born in the sector of their respective season.
              </p>
              <h2 className="systemTitle">MAGE RANKS</h2>
              <p className="systemDetail">
                The method of labeling all licensed mages of Koranin is measured through the current highest-leveled card unlocked in their spell deck through the following tiers: Scholars (2-4), Archmages (5-7), Regents (8-10) and High Mages (J-K). While a mage's rank does not account for their potential for magic, many government and prestigious positions are reserved only for High Mages due to the level of security and danger.
              </p>
            </p>
            <img src={down_arrow} className="down_arrow" onClick={toHomeSection} />
            <p className="arrow_instruct">return to top</p>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Home;