import React, { useState, useRef } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import '../../css/CharaDetails.scss';
import arrow from '../../img/down_arrow.png';
import { kingpinChara } from './Kingpin_Config';
import BrighteDetails from './brighte/Brighte_Collapse';
import LalehDetails from './laleh/Laleh_Collapse';
import LydiDetails from './lydi/Lydi_Collapse';
import NakoaDetails from './nakoa/Nakoa_Collapse';
import RinDetails from './rin/Rin_Collapse';
import RobynDetails from './robyn/Robyn_Collapse';
import SuminaDetails from './sumina/Sumina_Collapse';

const chara_details = [
    {
        name: "brighte",
        details: <BrighteDetails />
    },
    {
        name: "laleh",
        details: <LalehDetails />
    },
    {
        name: "lydi",
        details: <LydiDetails />
    },
    {
        name: "nakoa",
        details: <NakoaDetails />
    },
    {
        name: "rin",
        details: <RinDetails />
    },
    {
        name: "robyn",
        details: <RobynDetails />
    },
    {
        name: "sumina",
        details: <SuminaDetails />
    },
]

const Kingpin_Chara = () => {
    const [active, setActive] = useState("");
    const detailsSection = useRef(null);

    const _onClick = (name) => {
        setActive(name);
        window.scrollTo({ top: 1800, behavior: 'smooth' });
    }

    AOS.init({
        duration: 1200,
    })

    return (
        <div>
            <div className="selection_chara">
                {kingpinChara.map((character, index) =>
                    <div className="selection_chara_container" key={index} onClick={() => _onClick(character.name)}>
                        <p className={character.name + "Active selection_chara_name"}>{character.fullname.toUpperCase()}</p>
                        <div>
                            <img
                                src={character.icon}
                                className={(active === "" && "selection_chara_card selection_chara_card_active") || (active !== character.name ? "selection_chara_card selection_chara_card_inactive" : "selection_chara_card selection_chara_card_active")}
                                alt={character.name + "icon img"}
                            />
                            <p className="selection_chara_tagline">{character.tagline}</p>
                        </div>
                    </div>
                )}
                <p className="selectInstruct"></p>
            </div>
            <br></br>
            <div className="details_section">
                {kingpinChara.map((character) =>
                    active === character.name
                    &&
                    <div className="detail">
                        <div className={"detail_header_fade detail_header_fade_" + character.name}></div>
                        <div className={"detail_header detail_header_" + character.name}>
                            <div className="detail_header_container">
                                <h2 className="detail_header_name">{character.fullname}</h2>
                                <p className="detail_header_pronunciation">{character.pronunciation}</p>
                            </div>
                        </div>
                        <div className="detail_chara">
                            <br></br>
                            <div className="detail_chara_body">
                                <div className="detail_chara_display">
                                    <img src={character.fullbody}
                                        className="detail_chara_display_img_full"
                                        alt={character.name + "fullbody img"}
                                    />
                                </div>
                                <div className="detail_chara_info">
                                    <div className="detail_chara_info_container">
                                        <div className="detail_chara_info_container_hidden">
                                            <img src={character.sector_image}
                                                className="detail_chara_info_sectorbg"
                                                alt={character.sector + "logo"}
                                            />
                                        </div>
                                        <div className="detail_chara_info_container_more">
                                            <img src={character.card}
                                                className="detail_chara_info_img_card"
                                                alt={character.name + "card img"}
                                            />
                                            <div className="detail_chara_info_summary">
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>GENDER</p><p className="summary_attribute_highlight_value">{character.gender}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>AGE</p><p className="summary_attribute_highlight_value">{character.age}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>SPECIES</p><p className="summary_attribute_highlight_value">{character.species}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>STAR SIGN</p><p className="summary_attribute_highlight_value">{character.star_sign}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>SECTOR</p><p className="summary_attribute_highlight_value">{character.sector}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>MAGE RANK</p><p className="summary_attribute_highlight_value">{character.mage_rank}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>MAGIC SPECIALTY</p><p className="summary_attribute_highlight_value">{character.magic_specialty}</p></div>
                                                <div className="detail_chara_info_summary_att"><p className={"summary_attribute_highlight summary_attribute_highlight_" + character.name}>CONSTELLATION</p><p className="summary_attribute_highlight_value">{character.constellation}</p></div>
                                            </div>
                                            <div className="detail_chara_info_collapse">
                                                <div className="detail_chara_info_collapse_text" dir="rtl">
                                                    {chara_details.map((detail) =>
                                                        active === detail.name && <div>{detail.details}</div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <p className="sArrow_instruct" ref={detailsSection}>click to go back to top</p>
                            <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} alt="arrow" />
                        </div>
                    </div>
                )}
            </div>
        </div>
    )
}

export default Kingpin_Chara;