import React, { useState } from 'react'
import './css/Sectors.css';
import '../css/Home.css';
import up_arrow from '../img/up_arrow.png';
import down_arrow from '../img/down_arrow.png';
import alceriumIcon from '../img/alcerium_icon.png';
import castorixIcon from '../img/castorix_icon.png';
import valterreIcon from '../img/valterre_icon.png';
import lyrisiusIcon from '../img/lyrisius_icon.png';
import lysandeIcon from '../img/lysande_icon.png';
import Alcerium_Gallery from './sectors/alcerium/Alcerium_Gallery';
import Castorix_Gallery from './sectors/castorix/Castorix_Gallery';
import Valterre_Gallery from './sectors/valterre/Valterre_Gallery';
import Lyrisius_Gallery from './sectors/lyrisius/Lyrisius_Gallery';
import Lysande_Gallery from './sectors/lysande/Lysande_Gallery';

class Sectors extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAlcerium: true, 
            showCastorix: false,
            showValterre: false,
            showLyrisius: false,
            showLysande: false
        };
    }

    toggleAlcerium = () => {
        this.setState({ 
            showAlcerium: true, 
            showCastorix: false, 
            showValterre: false,
            showLyrisius: false,
            showLysande: false,
        });
    }

    toggleCastorix = () => {
        this.setState({ 
            showAlcerium: false, 
            showCastorix: true, 
            showValterre: false,
            showLyrisius: false,
            showLysande: false,
        });
    }

    toggleValterre = () => {
        this.setState({ 
            showAlcerium: false, 
            showCastorix: false, 
            showValterre: true, 
            showLyrisius: false,
            showLysande: false,
        });
    }

    toggleLyrisius = () => {
        this.setState({ 
            showAlcerium: false, 
            showCastorix: false, 
            showValterre: false,
            showLyrisius: true, 
            showLysande: false,
        });
    }

    toggleLysande = () => {
        this.setState({ 
            showAlcerium: false, 
            showCastorix: false, 
            showValterre: false,
            showLyrisius: false,
            showLysande: true, 
        });
    }

    render() {
        return (
            <div className="sector">
                {this.state.showAlcerium ? <Alcerium_Gallery/> : null}
                {this.state.showCastorix ? <Castorix_Gallery/> : null}
                {this.state.showValterre ? <Valterre_Gallery/> : null}
                {this.state.showLyrisius ? <Lyrisius_Gallery/> : null}
                {this.state.showLysande ? <Lysande_Gallery/> : null}
                <div className="details">
                    <p className="arrow_instruct">return to home</p>
                    <img src={up_arrow} className="up_arrow" onClick={() => window.scrollTo({top: 0, behavior: 'smooth'})}/>
                    <div className="section">
                        <br></br>
                        {this.state.showAlcerium ? <h className="title">Alcerium</h> : null}
                        {this.state.showCastorix ? <h className="title">Castorix</h> : null}
                        {this.state.showValterre ? <h className="title">Valterre</h> : null}
                        {this.state.showLyrisius ? <h className="title">Lyrisius</h> : null}
                        {this.state.showLysande ? <h className="title">Lysande</h> : null}
                    </div>
                    <div className="icon_section">
                        <img src={alceriumIcon} 
                            className={this.state.showAlcerium ? "active_icon" : "nonactive_icon"} 
                            onClick={this.toggleAlcerium}/>
                        <img src={castorixIcon} 
                            className={this.state.showCastorix ? "active_icon" : "nonactive_icon"} 
                            onClick={this.toggleCastorix}/>
                        <img src={valterreIcon} 
                            className={this.state.showValterre ? "active_icon" : "nonactive_icon"} 
                            onClick={this.toggleValterre}/>
                        <img src={lyrisiusIcon} 
                            className={this.state.showLyrisius ? "active_icon" : "nonactive_icon"} 
                            onClick={this.toggleLyrisius}/>
                        <img src={lysandeIcon} 
                            className={this.state.showLysande ? "active_icon" : "nonactive_icon"}
                            onClick={this.toggleLysande}/>
                    </div>
                    {this.state.showAlcerium ? <p className="excerpt">
                        <b>K</b>nown for its special healing flora and distinctive foods and spices, Alcerium is Koranin’s second largest territory. Otherwise named the spring sector, Alcerium is made up of a large rainforest bordered by a vast ocean territory and occupied largely by underwater creatures, along with those gifted with elemental or healing abilities.
                        </p> : null}
                    {this.state.showCastorix ? <p className="excerpt">
                        <b>A</b>lso known as the winter sector, Castorix is a snowy mountain region where most of humanity is structured underground or deep within the mountains. This is where Koranin’s flashiest infrastructure and technology is located, occupied heavily by creatures whose abilities resonate with the dark or light. Deeper underground lies the sector’s mines, where Koranin’s most precious minerals are procured. Stationed aboveground in the mountain ranges are military camps, crawling with shapeshifters and those of enhanced natural abilities to aid in hunt.
                        </p> : null}
                    {this.state.showValterre ? <p className="excerpt">
                        <b>T</b>he most diverse sector and the heart of the trade industry between territories, Valterre lies at the center of Koranin. A tall mountain range rings the sector, making it difficult to attack and therefore a safeguard for the stronghold of knowledge and academia. Aside from its lively, bustling cities, Valterre is where most officials and High Mages are stationed, and where countless others come in search of opportunity.
                        </p> : null}
                    {this.state.showLyrisius ? <p className="excerpt">
                        <b>S</b>pecializing in battle and strategy, the autumn sector’s brawns and brains largely outclass those of the other sectors. Consisting of floating abodes connected by wind passages, Lyrisius is arguably the most detested yet revered sector, for it is occupied by many elites and royals whose condescending attitudes are evenly backed by their eminence. Those blessed with flight ability and divination mastery often gracethe palaces of Lyrisius.
                        </p> : null}
                    {this.state.showLysande ? <p className="excerpt">
                        <b>K</b>oranin’s resident precinct of summer, Lysande is the largest sector and consists of a vast desert territory dotted with a subregion of tropical islandsalong its coast. Lysande serves as the main provider of Koranin’s textiles and weaponry forging, and is occupied largely by those of weather-related prowess, humans and other creatures alike.
                        </p> : null}
                    <img src={down_arrow} className="down_arrow" onClick={() => window.scrollTo({top: document.body.scrollHeight, behavior: 'smooth'})}/>
                    <p className="arrow_instruct">continue to index</p>
                </div>
            </div>
        )
    }
}

export default Sectors;