import React, { useState, useLayoutEffect } from 'react';
import { NavLink, Link, useLocation } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './css/Sectors.scss';
import jokerIcon from './img/joker_icon.png';
import map_splash from './img/splash_map.png';
import { sectors } from './components/sectors/Sectors_Config';

const World = () => {
    const location = useLocation();

    useLayoutEffect(() => {
        window.scrollTo(0, 0);
    }, [location.pathname])

    AOS.init({
        duration: 1200,
    })

    return (
        <div className="sector_body">
            <NavLink to="/joker"><img src={jokerIcon} className="jokerIcon" alt="joker icon" /></NavLink>
            <div className="sector_details">
                <div className="sector_section">
                    <img src={map_splash} className="sector_splash sector_splash_main" alt="sector splash art" />
                    <div className="sector_excerpt_container">
                        <p className="sector_excerpt">
                            Seasons come and go... but here in Koranin they are eternal... and magic binds all.
                        </p>
                    </div>
                    <div data-aos="fade-up" className="sector_selection">
                        {
                            sectors.map((sector) =>
                                <Link to={"/world" + sector.url} style={{ textDecoration: 'none' }}>
                                    <img src={sector.icon} className="sector_selection_icons" />
                                </Link>
                            )
                        }
                    </div>
                </div>
            </div>
            <br></br>
        </div>
    );
}

export default World;